* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  padding-top: 5em;
  background-color: #222831;
}

.square {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 6px;
  border: none;
  margin: 3px;
  font-size: 3em;
  font-weight: 800;
  cursor: pointer;
  background-color: #31363F;
  color: #76ABAE;
}

.square:hover {
  background-color: #565e6c;
}

#row {
  width: fit-content;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

h1 {
  color: #9ebabb;
  margin-bottom: 2em;
  font-size: 3em;
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

h1 span {
  color: #7cae76;
  padding-left: 0.2em;
}

.con {
  width: 310px;
  height: 320px;
  margin: auto;
}

#reset {
  position: relative;
  background-color: #14121b;
  color: rgba(255, 255, 255, 0.633);
  border-radius: 50px;
  padding: 10px 26px;
  border: none;
  font-weight: 600;
  font-size: 1.8em;
  cursor: pointer;
  margin-top: 30px;
  right: -30%;
}

#reset:hover {
  background-color: #222028;
}

@media (max-width:612px) {
  h1 {
    font-size: 2.6em;
  }
}


@media (max-width:500px) {
  h1 {
    font-size: 2.2em;
  }
}

@media (max-width:425px) {
  h1 {
    font-size: 1.8em;
  }
  .square {
    width: 70px;
    height: 70px;
    font-size: 2.6em;
  }
  .con {
    width: 210px;
  }
  #reset {
    font-size: 1.4em;
  }
}